import Homepage from './components/Homepage';
import PricingPage from './components/PricingPage';
import qnaPage from './components/qnaPage';
import AboutPage from './components/AboutPage';
import TipsPage from './components/TipsPage';
import InJurusanPage from './components/InJurusanPage';
import InUniPage from './components/InUniPage';
import Login from './components/LoginPage';
import ProfilePage from './components/ProfilePage'

export default[
    { path: '/', component: Homepage},
    { path: '/pricing', component: PricingPage},
    { path: '/qna', name: 'qna', component: qnaPage},
    { path: '/about', component: AboutPage},
    { path: '/Tips', component: TipsPage},
    { path: '/Jurusan', component: InJurusanPage},
    { path: '/Universitas', component: InUniPage},
    { path: '/profile', component: ProfilePage},
    { path: '/Login', component: Login},
    { path: '*', redirect: Homepage}
]

