              <div class="md-form form-sm mb-5">
                <i class="fas fa-envelope prefix"></i>
                <input type="email" id="modalLRInput12" class="form-control form-control-sm validate" v-model="logemail">
                <label data-error="wrong" data-success="right" for="modalLRInput12">Email</label>
              </div>

              <div class="md-form form-sm mb-4">
                <i class="fas fa-lock prefix"></i>
                <input type="password" id="modalLRInput11" class="form-control form-control-sm validate" v-model="logpassword">
                <label data-error="wrong" data-success="right" for="modalLRInput11">Password</label>
              </div>
              <div class="text-center mt-2">
                <button class="btn btn-info" @click="LogIn">Log in</button>
              </div>
            </div>
            <!--Footer-->
