        <p>Jika kamu ingin menjadi mahasiswa berprestasi tapi tak tahu caranya, berikut ini beberapa tipsnya :</p>
        <div class="left-text">
        <p><b>1. Review materi kuliah</b></p>
        <p>Tugas mahasiswa bukan sekadar datang untuk kuliah. Tapi, kamu juga harus memahami materi yang diberikan dosen. Supaya kamu memperoleh nilai yang bagus saat ujian, tentu yang dilakukan adalah belajar. Review kembali materi kuliah yang diajarkan secara rutin sehingga kamu enggak sistem kebut semalam saat ujian tiba.</p>
        <p><b>2. Dekat dengan dosen</b></p>
        <p>Mahasiswa berprestasi identik dekat dengan dosen. Sebab, dosen akan memercayakan beberapa tugas atau tanggung jawab kepadanya. Dekat dengan dosen memang banyak manfaatnya. Salah satunya, kamu akan mendapat berbagai informasi dan mudah mendapat rekomendasi jika dibutuhkan.</p>
        <p><b>3. Asah skill</b></p>
        <p>Mahasiswa berprestasi enggak cuma pintar di kelas. Kamu harus mengombinasikan hard skill dan soft skill. Ikutlah kursus atau pelatihan yang kamu sukai untuk menunjang karier di masa mendatang.</p>
        <p><b>4. Jalin jaringan yang luas</b></p>
        <p>Berprestasi tapi enggak punya banyak teman tentu percuma. Bangun jaringan dengan memperluas pergaulan. Jangan jadi mahasiswa kuper atau antisosial.</p>
        <p><b>5. Berorganisasi</b></p>
        <p>Menyeimbangkan kuliah dan berorganisasi memang tak mudah. Jika tak cerdik membagi waktu, alhasil nilai jadi jeblok. Oleh karena itu, pilih organisasi atau UKM yang sesuai hobi dan passion. Sebab, berorganiasi akan memberikan banyak pengalaman yang enggak kamu dapat saat berada dalam kelas kuliah.</p>
        <p><b>6. Menerima masukan</b></p>
