      <div class="modal-header">
        <h4 class="modal-title">Tips Memilih Jurusan Kuliah</h4>
      </div>
      <div class="modal-body">
        <p>Bagi mereka yang saat ini tengah berada di kelas XII, pikiran tak hanya terfokus untuk bisa lulus dengan nilai terbaik. Tapi juga akan melanjutkan ke mana setelah lulus nanti.</p>
        <p>Meski ada banyak perguruan tinggi yang tersedia saat ini, untuk memilih jurusan kuliah ternyata tidaklah mudah. Ada banyak pertimbangan yang harus dipikirkan, agar Anda tidak salah jurusan.</p>
        <p>Tahu kan bagaimana rasanya kalau ‘salah jurusan’? Hati dan pikiran yang tidak fokus akan berdampak buruk terhadap nilai yang didapat kelak. Nah, Anda tentu tidak mau hal itu terjadi pada diri Anda bukan?</p>
        <p>Karena itu, pastikan sebelum memilih jurusan di perguruan tinggi Anda membaca dan memahami tips-tips di bawah ini. Sehingga Anda akan terhindar dari kasus ‘salah jurusan’.</p>
        <p>Berikut ini 9 tips yang akan membantu Anda memilih jurusan kuliah yang tepat :</p>
        <div class="left-text">
          <p><b>1. Follow your passion</b></p>
          <p>Kunci dari memilih jurusan kuliah adalah mengetahui apa yang benar-benar Anda sukai. Tak hanya soal mata pelajaran sekolah, tapi juga aspek lainnya yang mungkin tidak berkaitan langsung dengan materi pelajaran.<br>Kunci dari memilih jurusan kuliah adalah mengetahui apa yang benar-benar Anda sukai. Tak hanya soal mata pelajaran sekolah, tapi juga aspek lainnya yang mungkin tidak berkaitan langsung dengan materi pelajaran.<br>Misalkan Anda suka dengan pelajaran eksakta seperti matematika atau IPA. Maka pilihlah jurusan yang memang akan banyak diisi oleh dua pelajaran tersebut. Agar minat dan semangat belajar tetap tinggi nantinya.<br>Begitu pula bagi Anda yang memang doyan menghapal tapi malah menghitung. Jurusan ilmu-ilmu sosial akan tetap untuk dipilih. Pastikan jurusan yang dipilih memang sesuai dengan minat dan passion Anda ya.</p>
          <p><b>2. Pahami potensi diri</b></p>
          <p>Selain mengetahui hal-hal yang disukai, Anda juga harus mengenal dan mengetahui potensi diri. Terkadang, potensi diri ini tidak selalu sejalan dengan minat dan passion. Tapi mampu memberi Anda panduan dalam menentukan jurusan yang akan dipilih.<br>Misalkan, Anda suka dengan pekerjaan sebagai programmer. Namun, Anda ternyata tak punya kemampuan dalam hal matematika dan logika. Maka, tinggalkan ketertarikan Anda tersebut sebelum menyesal di kemudian hari.<br>Namun demikian, potensi diri juga bisa berhubungan langsung dengan minat Anda. Itu artinya, Anda sudah mampu memanfaatkan potensi menjadi sebuah hobi yang bisa terus didalami. Mengenal potensi diri lebih dalam akan membuat Anda tidak kebingungan.</p>
          <p><b>3. Jangan ikut-ikutan pilihan teman</b></p>
          <p>Nah, ini penting bagi Anda yang masih bimbang dalam menentukan jurusan di perguruan tinggi. Jangan pernah mengambil jurusan karena sedang menjadi tren atau karena banyak dipilih oleh teman Anda.<br>Karena, seperti dijelaskan di poin sebelumnya, pemilihan jurusan harus berdasarkan minat dan potensi yang dimiliki. Jangan sekadar ikut pilihan teman, yang justru nantinya hanya menjerumuskan.<br>Yakinkan diri Anda bahwa jurusan kuliah Anda adalah yang tepat untuk Anda. Karena bisa saja jurusan A akan cocok bagi teman Anda tapi sangat tidak tepat untuk Anda sendiri. Begitu pul sebaliknya.<br>Jadi, buang jauh-jauh pikiran untuk menyamakan jurusan dengan teman-teman Anda. Nasib Anda berada di tangan Anda sendiri. Bukan orang lain.</p>
          <p><b>4. Cari kelebihan dan kekurangannya</b></p>
          <p>Sebenarnya ada satu cara sederhana untuk menentukan jurusan di perguruan tinggi. Cari kelebihan dan kekurangan dari jurusan yang akan Anda pilih.<br>Jangan hanya kelebihan saja ya. Karena ini akan membuat Anda terbuai dan tidak menyadari kesulitan yang bisa Anda hadapi saat kuliah nanti.<br>Lakukan riset terhadap jurusan yang diminati. Mulai dari jumlah siswa yang diterima, mata kuliah utama, hingga jumlah sks yang harus dipenuhi. Pastikan Anda memang sanggup mengikuti setiap mata kuliah yang akan didapat saat diterima di jurusan tersebut.<br>Jangan karena jurusan tersebut dipilih oleh banyak teman-teman Anda, maka Anda pun ikut memilihnya. Padahal belum tentu bakat dan passion Anda adalah di jurusan tersebut.<br>Bisa-bisa Anda keburu Drop Out karena tak sanggup hadapi mata kuliah yang berat.</p>
          <p><b>5. Ketahui tingkat akreditasi jurusan</b></p>
          <p>Satu hal yang tak boleh lewat saat memilih jurusan kuliah adalah tingkat akreditasi dari jurusan tersebut. Terutama jika Anda akan memilih jurusan di perguruan tinggi swasta. Tingkat akreditasi ini akan memberi gambaran tingkat kredibilitas dari jurusan tersebut.<br>Semakin tinggi akreditasi yang dimiliki jurusan tersebut, semakin bagus untuk Anda pilih. Karena tingkat kepercayaan jurusan tersebut di masyarakat pun akan tinggi.<br>Jangan lupa, tingkat akreditasi jurusan ini akan memengaruhi penilaian dari perusahaan saat Anda melamar pekerjaan kelak. Termasuk juga saat ingin melanjutkan pendidikan ke jenjang yang lebih tinggi.<br>Jadi, selalu perhatikan level akreditasi jurusan yang dipilih. Jangan sampai Anda hanta tergiur oleh bujukan orang lain tanpa memedulikan masa depan Anda.</p>
          <p><b>6. Perhatikan peluang kerja selesai kuliah</b></p>
          <p>Sasaran yang dituju saat selesai kuliah oleh para calon mahasiwa mayoritas memang menjadi pekerja atau karyawan. Akan lebih baik jika nanti bisa bekerja di perusahaan besar yang memang menjadi impian.<br>Nah, untuk itu perhatikan juga jurusan yang akan Anda pilih. Apakah jurusan tersebut memang memberi peluang kerja yang cukup besar atau tidak? Apakah kelak ada banyak perusahaan yang akan membutuhkan jasa Anda sebagai sarjana dari jurusan tersebut?<br>Meskipun hal ini tidak mutlak, karena faktanya ada banyak pekerjaan yang dijalani tanpa menyesuaikan jurusan atau pendidikan yang didapat. Tapi setidaknya hal ini akan membuat upaya Anda mendapatkan pekerjaan akan lebih mudah ketika selesai kuliah nanti.</p>
          <p><b>7. Pantau situasi global</b></p>
          <p>Di era globalisasi sekarang ini Anda juga harus pintar mengikuti dan memahami arah perubahan di dunia. Termasuk juga saat akan memilih sebuah jurusan di perguruan tinggi.<br>Apa yang menjadi tren di dunia dan Indonesia saat ini menjadi salah satu patokan saat akan melanjutkan pendidikan.Tapi cermati juga apakah tren tersebut akan bertahan dalam 5-10 tahun ke depan? Sehingga saat selesai kuliah nanti, ada banyak pekerjaan yang siap menampung Anda.</p>
          <p><b>8. Perhatikan biaya kuliah</b></p>
          <p>Dalam memilih jurusan kuliah perhatikan juga biaya kuliah yang harus Anda keluarkan. Bukan biaya 1-2 tahun saja. Tapi biaya keseluruhan, dari sejak semester pertama hingga selesai sidang skripsi kelak.<br>Ambil contoh, biaya kuliah yang dibutuhkan di jurusan kedokteran dengan sastra atau bahasa akan jauh berbeda. Karena ada banyak praktik yang harus dilakukan oleh para calon dokter dibanding calon sarjana bahasa.<br>Kalau memang orang tua Anda kesulitan membiayai kuliah Anda nanti, jangan memaksakan diri. Meskipun nanti Anda bisa nyambi bekerja sambil kuliah, hal ini akan mengganggu fokus Anda dalam belajar. Ujungnya, masa kuliah Anda akan molor dari waktu yang sudah ditentukan.<br>Memang ada solusi dengan mendapatkan beasiswa. Namun, hal ini pun tidak mudah. Karena beasiswa hanya akan diberikan kepada mereka yang tidak mampu atau punya nilai rata-rata akademik yang tinggi semasa di SMA.</p>
          <p><b>9. Minta pendapat orang lain</b></p>
          <p>Terakhir, jangan pernah sungkan untuk meminta pendapat dari orang lain. Terutama dari orang tua, guru, atau saudara yang pernah kuliah. Cari informasi tentang jurusan yang Anda minati kepada mereka.<br>Gali lebih dalam juga soal passion dan bakat yang Anda miliki dari mereka. Semakin banyak informasi yang diperoleh akan semakin baik untuk Anda saat menentukan jurusan di perguruan tinggi.</p>
          <hr>
          <p><b>Penutup</b></p>
          <p>Memilih jurusan kuliah memang tidak sama dengan jodoh dan rezeki yang sudah ditentukan oleh Tuhan. Anda harus melakukan riset yang mencukupi untuk bisa mengetahui jurusan yang diminati memang cocok dan tepat dengan diri Anda.</p>
          
        </div>
