      <div class="modal-header">
        <h4 class="modal-title">Cara Menemukan Teman Baru di Kampus</h4>
      </div>
      <div class="modal-body">
        <p>Enggak sedikit dari kita yang rupanya kesulitan untuk menemukan teman baru di kampus. Efeknya, hal tersebut akan berpengaruh pada beberapa aspek, mulai dari proses belajar mengajar, sampai masalah asmara kita di kampus.</p>
        <p>Nah, buat kamu yang punya masalah serupa, enggak usah khawatir. Soalnya, dikutip dari Teen Vogue, <i>U-City</i> merangkum lima tips cara mendapatkan teman baru di kampus, supaya kamu enggak sendirian lagi.</p>
        <div class="left-text">
          <p><b>1. Belajar menyesuaikan diri sesegera mungkin</b></p>
          <p>Memasuki kampus, suasana, dan lingkungan baru bukan merupakan hal yang mudah. Hidup kita akan dikelilingi orang asing. Menurut Teen Vogue, semua mahasiswa baru umumnya akan merasakan kecanggungan seperti ini, di hari pertama kuliah. Namun seiring berjalannya waktu, proses transisi ini akan semakin membaik. Kamu hanya perlu membuka diri pada hal-hal baru di kampus.</p>
          <p><b>2. Jadi diri sendiri</b></p>
          <p>Terkadang cara kita memulai pertemanan dengan orang lain enggak selalu ditanggapi dengan baik. Teen Vogue menyarankan agar tetap menjadi diri sendiri apapun yang terjadi. Kamu enggak perlu menjadi seperti orang lain supaya dapat banyak teman. Pertemanan yang sifatnya dipaksakan enggak akan pernah berjalan dengan baik.</p>
          <p><b>3.Terlibat dalam kegiatan kampus</b></p>
          <p>Salah satu cara terbaik dalam mendapatkan banyak teman adalah terjun aktif di kegiatan kampus. Lewat cara ini kamu akan mendapatkan banyak kenalan. Kamu akan mendapat kenalan orang-orang baru yang memiliki minat sama denganmu.</p>
          <p><b>4. Ambil kesempatan tinggal di asrama</b></p>
          <p>Jika kamu tinggal di asrama, kamu akan tinggal bersama orang dengan berbagai latar belakang dalam satu atap. Manfaatkan kesempatan ini dengan banyak berkenalan dengan teman sekamarmu. Kamu juga harus aktif dalam kegiatan asrama, sehingga lama-kelamaan kamu akan mengenal situasi dan seluk-beluk asrama serta seluruh orang di dalamnya.</p>
          <p><b>5. Ubah kenalan menjadi teman</b></p>
          <p>Beberapa kenalan yang kamu temui di kampus, terkadang dapat menjadi teman potensial di masa depan. Jika kamu orang yang suka bergaul, manfaatkan kesempatan ini untuk mengakrabkan diri dengan kenalan kamu. Terkadang saat kamu kesulitan, mereka bisa menjadi penolong yang dapat diandalkan untuk mengatasi masalah-masalah kamu.</p>
        </div>


