      <div class="modal-header">
        <h4 class="modal-title">Mengenal Diri Sendiri</h4>
      </div>
      <div class="modal-body">
        <p>Sukses adalah impian banyak orang. Tetapi masih banyak orang bingung bagaimana cara untuk meraihnya? Kamu tidak hanya perlu kerja keras tetapi juga perlu memahami dirimu saat ingin meraih sukses.</p>
        <p>Mengenal diri sendiri lebih dalam akan membantu mengerti apa yang sebenarnya dirimu inginkan untuk menuju sukses. Apa saja yang perlu dilakukan? Berikut <i>U-City</i> bagi tips untuk memahami dirimu.</p>
        <div class="left-text">
          <p><b>1. Tanyakan apa yang kamu inginkan dalam hidup</b></p>
          <p>Yang paling utama adalah kamu harus mengerti apa yang kamu mau. Sebenarnya apa yang kamu inginkan dalam hidup ini dan sukses seperti apa yang kamu inginkan. Jika kamu sudah mengerti keinginanmu yang paling kuat maka kamu bisa berjalan menuju ke pintu sukses lebih terarah.</p>
          <p><b>2. Tulis sifat baik dan burukmu dibuku</b></p>
          <p>Dengan mengenal sifat-sifat baik dan burukmu, maka kamu akan lebih mengerti siapa sesungguhnya dirimu. Jika kamu mengetahui baik kelebihan dan kekurangan, maka kamu pastinya bisa menemukan solusi untuk memecahkan masalah yang kamu temui.</p>
          <p><b>3. Tingkatkan apa yang baik dan kurangi yang buruk dalam dirimu</b></p>
          <p>Kamu sudah tahu kelebihan dan kelemahanmu, maka ini saatnya kamu memupuk apa yang menjadi kelebihanmu. Kamu juga perlu mencari solusi untuk mengatasi apa yang kamu miliki sebagai kekuranganmu. Dengan begitu, kamu akan lebih mengerti apa yang kamu perlu.</p>
          <p><b>4. Cari apa yang kamu sukai</b></p>
          <p>Jika kamu mempunyai passion atau hobi, sebaiknya ikuti passion tersebut. Menggeluti sesuatu yang kamu senangi tentunya akan berdampak baik buat meraih kesuksesan. Ingat bahwa tidak pernah ada kata terlambat untuk belajar, jadi dalami apa yang kamu senangi.</p>
          <p><b>5. Banyak baca buku biografi</b></p>
          <p>Dalam hidup pastinya kamu perlu inspirasi. Kamu bisa mendapat inspirasi tersebut dari hal-hal di sekitarmu. Selain itu membaca biografi-biografi orang sukses juga adalah salah satu cara untuk memotivasi dirimu agar ingin maju.</p>
          <p><b>6. Jangan menjiplak cara hidup orang</b></p>
          <p>Orang yang ingin sukses sebaiknya berusaha dengan caranya sendiri dari pada menjiplak cara hidup yang dimiliki oleh orang lain. Jika kamu menjiplak hidup orang lain, itu juga akan berbahaya buat kamu karena sebenarnya kamu tidak mengerti penuh tentang apa yang kamu tiru tersebut.</p>
          <p><b>7. Berjalanlah dengan percaya diri</b></p>
          <p>Berjalanlah dengan mantap tanpa rasa ragu yang mengganggu begitu dalam didalam dirimu. Kamu bisa menjadi orang yang percaya diri. Hal ini sangat perlu sehingga kamu tidak malu untuk melakukan sesuatu yang berhubungan dengan meraih sukses.</p>
          <p><b>8. Jika tidak suka, jangan dipaksakan</b></p>
          <p>Kamu mencoba sesuatu yang awalnya kamu anggap menarik, kemudian hal tersebut membuatmu kecewa. Hal ini perlu kamu sadari bahwa, memaksakan sesuatu yang sebenarnya tidak kamu cintai dapat menyusahkan dirimu sendiri.</p>
          <p><b>9. Pilihlah hal yang membuat bahagia</b></p>
          <p>Ini adalah pilihan yang menyenangkan karena kamu perlu menggeluti apa yang membuatmu bahagia. Jika kamu merasa bahagia maka kamu akan mencoba mencintai hal yang sedang kamu tekuni tersebut sehingga menghasilkan hal baik dan dekat dengan pintu kesuksesan.</p>
          <p><b>10. Cintai dirimu sebelum mencintai orang lain</b></p>
          <p>Kamu mempunyai cita-cita untuk berbuat baik dengan orang lain. Hal itu sangat baik tetapi perlu kamu ingat bahwa mencintai diri sendiri terlebih dulu itu sangat penting. Dengan mencintai dirimu, maka kamu merasa penuh dan mencintai orang lain akan terasa indah dan mudah.</p>
          <p><b>11. Banyaklah merenungkan apa yang sudah dan ingin kamu raih</b></p>
          <p>Saat kamu merenungkan hal yang sudah dan ingin kamu raih, maka hal itu akan membuatmu lebih mudah berbenah. Ketika ada yang kurang, kamu bisa membenahinya dan jika sudah baik kamu bisa menambahnya agar lebih luar biasa hasilnya.</p>
